import React, { Component } from 'react'

export class Menu2 extends Component {
    render() {
        return (
            <div>
                <p>Our elite team of consultants can aid you with a wide variety of specialist services. For example, we can augment your technical capabilities for special projects, design cost-effective and reliable procedures, or supply expert manpower to help you through particularly heavy workload periods. We can also assist you as arbitrators in examination disputes.<br/>We offer an Outside Agency (OA) Level III Services Program, which can provide on-going monitoring of a client's NDT Program. Benefits of the program :</p>
                <p><strong>1. LEVEL III Technical Support</strong></p>
                <p>SAR NDT Services technical personnel are well-known in the NDT field and carry multiple Level III certifications. They are proven instructors and specialists with many years of experience, and have broad experience encompassing many industries and product types. They are available to assist when there are critical problems to solve or when project needs demand additional expertise.</p>
                <p><strong>2. Written Practice</strong></p>
                <p>The NDT Written Practice provides the overall foundation for personnel qualification. It is a critical component to an effective NDT program. As part of the OA program, a SAR NDT Services Level III will review a company's Written Practice to assure it is up to date and complies with the applicable requirements. SAR NDT Services can also prepare a customized Written Practice if needed, and will assiste in defining the training, experience and examination requirements for each NDT qualification level and method.</p>
                <p><strong>3. Certification Examinations</strong></p>
                <p>SAR NDT Services Level III personnel are available to develop and administer NDT examinations to meet the requirements of the Written Practice and customer specifications. These examinations include General, Specific, and Practical for Level I and Level II candidates, and Basic, Method/General, Specific and Practical for Level III personnel. The results of these examinations are reported to the employer along with recommendations for certification if statisfactory.</p>
                <p><strong>4. Audit Services</strong></p>
                <p>SAR NDT Services personnel are available to perform audits to confirm that company Quality Assurance requirements are being met. Audits can also be performed at subcontractor locations.</p>
                <p><strong>5. NDT Procedures</strong></p>
                <p>NDT procedures are the company's "handbook"  for consistently performing Nondestructive Tests. SARNDT Services can prepare NDT procedure and ensure that they effectively describe the scope, equipment, inspection process and report formats required. SAR NDT Service's experience in a broad range of industries brings "best practices" to the initial development or revision of NDT procedures, specialized techniques for unique applications, and recommendations for the most appropriate equipment/accessories.</p>
            </div>
        )
    }
}

export default Menu2
